<template>
  <div>
    <div class="bukti-pelaporan">
      <h1 class="content-header">{{ this.$route.name }}</h1>
      <div v-if="$route.params.render == 'daftar'" class="content">
        <base-header class="pb-6" type="primary">
          <div class="row">
            <div class="col-lg-12 py-2 col-5 text-right">
              <el-select
                v-show="$store.getters.isAdmin"
                v-model="uk_id"
                placeholder="Pilih Unit Kerja"
                size="mini"
                @change="getList(uk_id), getILID"
                class="mr-2"
              >
                <el-option
                  v-for="option in cabang"
                  :key="option.label"
                  :label="option.label"
                  :value="option.value"
                ></el-option>
              </el-select>
              <el-select
                v-model="periode"
                size="mini"
                placeholder="Pilih Periode"
                class="mr-2"
                @change="changePeriode(periode)"
              >
                <el-option
                  v-for="option in listPeriode"
                  :key="option.label"
                  :label="option.label"
                  :value="option.value"
                ></el-option>
              </el-select>
              <base-button size="sm" type="neutral" @click="showForm"
                >Tambah</base-button
              >
            </div>
          </div>
        </base-header>
        <div class="container-fluid mt--6">
          <div>
            <div class="card">
              <div class="card-header">
                <h2 class="m-0">Daftar Bukti Pelaporan</h2>
              </div>
              <div>
                <el-table
                  :data="queriedData"
                  row-key="id"
                  header-row-class-name="thead-light"
                  @sort-change="sortChange"
                  @row-click="toDetail"
                  :default-sort="{ prop: 'IL_DATE', order: 'descending' }"
                >
                  <el-table-column
                    prop="IL_DATE"
                    sortable
                    label="Tanggal"
                    align="center"
                  >
                    <template v-slot="{ row }">
                      <div class="cell">{{ handlerDate(row.IL_DATE) }}</div>
                    </template>
                  </el-table-column>
                  <el-table-column
                    sortable
                    v-for="column in tableColumns"
                    :key="column.label"
                    v-bind="column"
                  ></el-table-column>
                </el-table>
              </div>
              <div
                slot="footer"
                class="col-12 px-4 py-2 d-flex justify-content-center justify-content-sm-between flex-wrap"
              >
                <div class="d-flex align-items-center">
                  <p class="card-category mb-0 mr-2">Menampilkan</p>
                  <div style="width: 80px">
                    <el-select
                      class="select-primary pagination-select"
                      v-model="pagination.perPage"
                      placeholder="Per page"
                      size="mini"
                    >
                      <el-option
                        class="select-primary"
                        v-for="item in pagination.perPageOptions"
                        :key="item"
                        :label="item"
                        :value="item"
                      ></el-option>
                    </el-select>
                  </div>
                  <p class="card-category m-0 ml-2">
                    Tampil {{ from + 1 }} sampai {{ to }} dari {{ total }} data
                    <span v-if="selectedRows.length"
                      >&nbsp; &nbsp; {{ selectedRows.length }} rows
                      selected</span
                    >
                  </p>
                </div>
                <base-pagination
                  class="pagination-no-border my-3"
                  v-model="pagination.currentPage"
                  :per-page="pagination.perPage"
                  :total="total"
                ></base-pagination>
              </div>
            </div>
          </div>
        </div>
      </div>
      <template v-if="$route.params.render == 'form'">
        <base-header class="pt-4 pb-6" type="primary" />
        <div class="container-fluid mt--6">
          <div class="card border-0">
            <div class="card-header">
              <h2 class="m-0">Tambah Bukti Pelaporan</h2>
            </div>
            <div class="card-body">
              <base-input
                v-show="$store.getters.isAdmin"
                label="Unit Kerja"
                class="form-group"
              >
                <el-select
                  v-model="uk_id"
                  @change="getILID"
                  placeholder="Pilih Unit Kerja"
                >
                  <el-option
                    v-for="option in cabang"
                    :key="option.label"
                    :label="option.label"
                    :value="option.value"
                  ></el-option>
                </el-select>
              </base-input>
              <base-input
                label="Periode"
                placeholder="Periode"
                class="form-group"
              >
                <el-select
                  v-model="formBuktiPelaporan.bp_periode"
                  placeholder="Pilih Periode"
                >
                  <el-option
                    v-for="option in listPeriode"
                    :key="option.label"
                    :label="option.label"
                    :value="option.value"
                  ></el-option>
                </el-select>
              </base-input>
              <base-input
                v-model="formBuktiPelaporan.bp_instansi"
                label="Judul Laporan"
                placeholder="Masukkan Judul Laporan"
                class="form-group"
              ></base-input>

              <h5>File</h5>

              <form
                class="mb-3"
                method="post"
                @change="insertName()"
                action="#"
                enctype="multipart/form-data"
              >
                <div class="input-group">
                  <div class="custom-file">
                    <input
                      type="file"
                      class="custom-file-input"
                      ref="buktiImg"
                      id="inputGroupFile01"
                    />
                    <label
                      v-if="!buktiPelaporanName"
                      class="custom-file-label"
                      for="inputGroupFile01"
                      >Klik Untuk Memilih File</label
                    >
                    <label
                      v-if="buktiPelaporanName"
                      class="custom-file-label"
                      for="inputGroupFile01"
                      >{{ buktiPelaporanName }}</label
                    >
                  </div>
                  <div class="input-group-append">
                    <button
                      class="btn base-button btn-primary"
                      type="button"
                      @click="submitFiles()"
                    >
                      Upload
                    </button>
                    <button
                      v-if="formBuktiPelaporan.bp_doclink"
                      class="btn base-button btn-danger"
                      type="button"
                      @click="clearImg()"
                    >
                      Hapus
                    </button>
                  </div>
                </div>

                <base-progress
                  v-if="progress_bp_doclink >= 1"
                  class="mb-0"
                  :type="progress_bp_doclink >= 100 ? 'green' : 'orange'"
                  :value="progress_bp_doclink"
                ></base-progress>
                <img
                  class="p-3 mx-auto img-fluid mb-3 d-block"
                  v-if="formBuktiPelaporan.bp_doclink"
                  :src="formBuktiPelaporan.bp_doclink"
                />
              </form>

              <div class="col-lg-12 px-0 py-2 col-5 text-right">
                <base-button size="md" type="danger" @click="hideForm"
                  >Kembali</base-button
                >
                <base-button
                  v-if="!isUpdate"
                  size="md"
                  type="primary"
                  @click="submitForm"
                  >Simpan</base-button
                >
                <base-button
                  v-if="isUpdate"
                  size="md"
                  type="primary"
                  @click="submitUpdateForm"
                  >Update</base-button
                >
              </div>
            </div>
          </div>
        </div>
      </template>
      <template v-if="$route.params.render == 'detail'">
        <base-header class="pb-6" type="primary">
          <div class="row">
            <div class="col-lg-12 py-2 col-5 text-right">
              <base-button
                @click="toUpdateForm"
                class="ml-2"
                size="sm"
                type="neutral"
                >Ubah</base-button
              >
            </div>
          </div>
        </base-header>
        <div class="container-fluid mt--6">
          <div class="card border-0">
            <div class="card-header">
              <h2 class="m-0">Bukti Pelaporan</h2>
            </div>
            <div class="card-body">
              <div class="col-lg-12 py-2 col-5 text-left">
                <div class="row">
                  <h3 class="col-2">Periode</h3>
                  <p class="col-10">
                    {{ formBuktiPelaporanDetail.BP_PERIODE }}
                  </p>
                  <hr class="col-12 mt-0 mb-4" />
                </div>
                <div class="row">
                  <h3 class="col-2">Instansi</h3>
                  <p class="col-10">
                    {{ formBuktiPelaporanDetail.BP_INSTANSI }}
                  </p>
                  <hr class="col-12 mt-0 mb-4" />
                </div>
                <div class="row">
                  <h3 class="col-2">File</h3>
                  <div class="col-10">
                    <img
                      class="img-fluid"
                      :src="formBuktiPelaporanDetail.BP_DOCLINK"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
import HtmlEditor from "@/components/Inputs/HtmlEditor";
import DropzoneFileUpload from "@/components/Inputs/DropzoneFileUpload";
import flatPicker from "vue-flatpickr-component";
import "flatpickr/dist/flatpickr.css";
import { Table, TableColumn, Select, Option } from "element-ui";
import { BasePagination, BaseInput } from "@/components";
import clientPaginationMixin from "@/components/clientPaginationMixin";
import swal from "sweetalert2";
import users from "./users2";
import axios from "axios";
import moment from "moment";
import cabang from "@/views/Pages/cabang";
import router from "../../../routes/router";
import listPeriode from "@/views/Pages/listPeriode.js";

export default {
  mixins: [clientPaginationMixin],
  components: {
    listPeriode,
    HtmlEditor,
    BasePagination,
    BaseInput,
    flatPicker,
    DropzoneFileUpload,
    [Select.name]: Select,
    [Option.name]: Option,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      periode: "",
      listPeriode,
      isUpdate: false,
      bukti_id: null,
      progress_bp_doclink: 0,
      cabang,
      optionUK: [{ value: 1, label: "Uk1" }],
      uk_id: null,
      buktiPelaporanName: "",
      formBuktiPelaporan: {
        il_id: 0,
        bp_doclink: "",
        bp_periode: "",
        bp_instansi: ""
      },
      formBuktiPelaporanDetail: {
        bp_doclink: "",
        bp_periode: "",
        bp_instansi: ""
      },
      dates: {
        simple: new Date("2020-10-10"),
        range: "2019-04-19 to 2019-04-24"
      },
      isTable: true,
      isForm: false,
      isDetail: false,
      propsToSearch: ["name", "email", "age"],
      tableColumns: [
        // {
        //   prop: "IL_DATE",
        //   label: "Tanggal",
        //   minWidth: 150,
        // },
        {
          prop: "UK_NAME",
          label: "Unit Kerja",
          minWidth: 150
        },
        {
          prop: "PERIODE",
          label: "Periode",
          minWidth: 150
        },
        {
          prop: "BP_INSTANSI",
          label: "Judul Laporan",
          minWidth: 150
        }
      ],
      tableData: [],
      selectedRows: []
    };
  },
  created() {
    this.getList(this.$store.getters.getUKId);
    if (!this.$store.getters.isAdmin) {
      this.uk_id = this.$store.getters.getUKId;
    }
    this.getILID();
    // this.getListPeriode();
  },
  methods: {
    changePeriode(value) {},
    getListPeriode() {
      axios
        .get("http://45.127.133.75:3000/api/iems/list-periode")
        .then(res => {
          console.log(res.data);
          let tempData = [];
          res.data.forEach(item => {
            tempData.push({
              name: item.PERIODE,
              value: item.PERIODE
            });
          });
          console.log(tempData);
          this.listPeriode = tempData;
        })
        .catch(err => {
          console.log(err);
        });
    },
    handlerDate(date) {
      return moment(date * 1000).format("DD/MM/YYYY");
    },
    toUpdateForm() {
      this.isUpdate = true;
      this.formBuktiPelaporan.il_id = this.formBuktiPelaporanDetail.IL_ID;
      this.formBuktiPelaporan.bp_periode = this.formBuktiPelaporanDetail.BP_PERIODE;
      this.formBuktiPelaporan.bp_instansi = this.formBuktiPelaporanDetail.BP_INSTANSI;
      this.formBuktiPelaporan.bp_doclink = this.formBuktiPelaporanDetail.BP_DOCLINK;
      this.bukti_id = this.formBuktiPelaporanDetail.BP_ID;
      this.$router.push("form");
    },
    toDetail(row, col, event) {
      this.formBuktiPelaporanDetail = row;
      console.log(row);
      this.$router.push("detail");
    },
    submitUpdateForm() {
      let formData = this.formBuktiPelaporan;
      if (formData.il_id == 0) {
        swal.fire({
          title: `Error`,
          text: "Silakan Pilih Unit Kerja!!!",
          buttonsStyling: false,
          confirmButtonClass: "btn btn-danger",
          icon: "error"
        });
        return;
      }
      axios
        .put(
          "http://45.127.133.75:3000/api/iems/bukti/" + this.bukti_id,
          formData
        )
        .then(res => {
          console.log(res.data);
          swal
            .fire({
              title: `Success`,
              text: "Bukti Pelaporan Terupdate",
              buttonsStyling: false,
              confirmButtonClass: "btn btn-success",
              icon: "success"
            })
            .then(result => {
              this.getList(this.$store.getters.getUKId);
              this.$router.push("daftar");
            });
        })
        .catch(err => {
          console.log(err);
          swal.fire({
            title: `Error`,
            text: "Terjadi kesalahan!!!\n" + err,
            buttonsStyling: false,
            confirmButtonClass: "btn btn-danger",
            icon: "error"
          });
        });
    },
    submitForm() {
      let formData = this.formBuktiPelaporan;
      if (formData.il_id == 0) {
        swal.fire({
          title: `Error`,
          text: "Silakan Pilih Unit Kerja!!!",
          buttonsStyling: false,
          confirmButtonClass: "btn btn-danger",
          icon: "error"
        });
        return;
      }
      axios
        .post("http://45.127.133.75:3000/api/iems/bukti", formData)
        .then(res => {
          swal
            .fire({
              title: `Success`,
              text: "Bukti Pelaporan Tersimpan",
              buttonsStyling: false,
              confirmButtonClass: "btn btn-success",
              icon: "success"
            })
            .then(result => {
              this.getList(this.$store.getters.getUKId);
              this.$router.push("daftar");
            });
        })
        .catch(err => {
          console.log(err);
          swal.fire({
            title: `Error`,
            text: "Terjadi kesalahan!!!\n" + err,
            buttonsStyling: false,
            confirmButtonClass: "btn btn-danger",
            icon: "error"
          });
        });
    },
    getList(data) {
      axios
        .get("http://45.127.133.75:3000/api/iems/bukti/" + data)
        .then(res => {
          let tempTable = res.data;
          // tempTable.forEach((table) => {
          //   table.IL_DATE = moment(table.IL_DATE).format("L");
          // });
          this.tableData = tempTable;
        })
        .catch(err => {
          console.log(err);
        });
    },

    submitFiles() {
      let formData = new FormData();
      formData.append("picture", this.$refs.buktiImg.files[0]);
      axios
        .post("http://45.127.133.75:3000/api/iems/img", formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          },
          onUploadProgress: function(progressEvent) {
            this.progress_bp_doclink = parseInt(
              Math.round((progressEvent.loaded / progressEvent.total) * 100)
            );
          }.bind(this)
        })
        .then(res => {
          this.formBuktiPelaporan.bp_doclink =
            "http://45.127.133.75:3000" + res.data.path;
        });
    },
    insertName() {
      this.buktiPelaporanName = this.$refs.buktiImg.files[0].name;
    },
    clearImg() {
      this.progress_bp_doclink = 0;
      this.formBuktiPelaporan.bp_doclink = "";
      this.buktiPelaporanName = null;
    },

    getILID() {
      axios
        .get("http://45.127.133.75:3000/api/iems/il-latest/" + this.uk_id)
        .then(res => {
          this.formBuktiPelaporan.il_id = res.data[0].IL_ID;
        });
    },
    showForm() {
      this.isUpdate = false;
      this.$router.push("form");
    },
    hideForm() {
      this.$router.push("daftar");
    }
  }
};
</script>
<style scoped>
.no-border-card .card-footer {
  border-top: 0;
}
.pagin {
  box-shadow: none;
  margin: 0 10px 0 10px;
  border-color: #b4cce1;
  border-radius: 10%;
  width: 66px;
  text-align: center;
  height: 30px;
}
</style>

<style lang="scss">
.bukti-pelaporan {
  .el-select--mini {
    .el-input--mini {
      input {
        height: 28px;
      }
    }
  }
}
</style>
